import React, { useState, useEffect } from "react";
import "./weather.css"; //   CSS  

function Weather() {
  const [weather, setWeather] = useState(null); //    
  const [date, setDate] = useState(""); //    

  //   fetch 
  const fetchWeather = async () => {
    try {
      const weatherResponse = await fetch(
        `https://182.218.49.58:50006/get_weather`
      );
      if (!weatherResponse.ok) {
        throw new Error(`Error: ${weatherResponse.status}`);
      }
      const weatherData = await weatherResponse.json();
      setWeather(weatherData);
      console.log(weatherData);
    } catch (error) {
      console.error("Error fetching weather data", error);
      setWeather(null);
    }
  };

  //     5   
  useEffect(() => {
    fetchWeather(); //      
    const intervalId = setInterval(() => fetchWeather(), 300000); // 5 
    return () => clearInterval(intervalId); //    
  }, []);

  //   useEffect
  useEffect(() => {
    const date = new Date();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const weekDays = ["", "", "", "", "", "", ""];
    const dayOfWeek = weekDays[date.getDay()];

    const formattedDate = `${month} ${day} (${dayOfWeek})`;
    setDate(formattedDate); //   
  }, []);

  return (
    <div>
      {weather ? (
        <div className="grid-container">
          <div className="city">  </div>
          <div className="today" id="today-date">
            {date} {/*   */}
          </div>
          <div className="weather">
            <img
              className="weatherIcon"
              src={weather.weather_icon}
              alt={weather.weather}
              width="100"
              height="100"
            />
            <p className="temp"></p> {/*    */}
          </div>
          <div className="temp">
            <span className="temp_num">{weather.current_temp}</span>
          </div>
          <div className="temp">
            <span className="temp_num">{weather.feeling_temp}</span>
          </div>
          <div className="max">
            : <span className="max_color">{weather.max_temp}</span>
          </div>
          <div className="min">
            : <span className="min_color">{weather.min_temp}</span>
          </div>
        </div>
      ) : (
        <div>Loading...</div> //   
      )}
    </div>
  );
}

export default Weather;
